<template>
  <div id="app">
    <h1>To-Do List</h1>
    <form @submit.prevent="addTasks">
      <input v-model="newTasks" placeholder="Enter a new task" />
      <button type="submit">Add</button>
    </form>
    <ul>
      <li v-for="task in tasks" :key="tasks.id">
        <span
          :style="{ textDecoration: tasks.done ? 'line-through' : 'none' }"
          @click="toggleTasks(tasks.id)"
        >
          {{ tasks.text }}
        </span>
        <button @click="deleteTasks(tasks.id)">Delete</button>
      </li>
    </ul>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      tasks: [],
      newTasks: "",
    };
  },
  async created() {
    this.tasks = await this.fetchTasks();
  },
  methods: {
    async fetchTasks() {
      const response = await axios.get("http://localhost:3000/api/tasks");
      return response.data;
    },
    async addTasks() {
      if (this.newTasks.trim()) {
        const response = await axios.post("http://localhost:3000/api/tasks", {
          text: this.newTasks,
        });
        this.tasks.push(response.data);
        this.newTasks = "";
      }
    },
    async toggleTasks(id) {
      const response = await axios.put(`http://localhost:3000/api/tasks/${id}`);
      const index = this.tasks.findIndex((t) => t.id === id);
      this.tasks[index] = response.data;
    },
    async deleteTasks(id) {
      await axios.delete(`http://localhost:3000/api/tasks/${id}`);
      this.tasks = this.tasks.filter((t) => t.id !== id);
    },
  },
};
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  text-align: center;
  margin: 2rem;
}
</style>